.gallary{
    padding-top: 120px;
    padding-bottom: 182px;
    position: relative;
}

.gallary__wrap {
    position: relative;
    margin-bottom: 50px;
    flex-direction: column;
    text-align: center;

    @media (max-width: 767px) {
        margin-bottom: 30px;
    }
}

.gallary__title {
    margin-bottom: 30px;
    max-width: 900px;
    font-family: 'Montserrat-bold';
    font-size: 64px;
    line-height: 78px;
    text-transform: uppercase;
    letter-spacing: -0.03em;

    @media (max-width: 1080px) {
        font-size: 60px;
        line-height: 68px;
    }

    @media (max-width: 767px) {
        margin-bottom: 10px;
        font-size: 24px;
        line-height: 29px;
        text-transform: unset;
    }
}

.gallary__subtitle {
    font-size: 36px;
    background: linear-gradient(270deg, #387DFB 0%, #00F2FB 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: block;
    text-align: center;
    line-height: 53px;
    letter-spacing: 0.015em;
    text-transform: uppercase;
    font-family: 'Oswald Regular';

    @media (max-width: 1080px) {
        font-size: 28px;
        line-height: 35px;
    }

    @media (max-width: 767px) {
        font-size: 16px;
        line-height: 24px;
    }
}

.gallary__shadows{
    position: absolute;
    width: 100%;
    height: auto;
    top: -300px;
    animation: 2s showElem infinite ease-in-out;
    animation-direction: alternate;
}

.gallary__slide{
    max-width: 900px;
    border-radius: 10px;
    overflow: hidden;

    @media (max-width: 1023px) {
        max-width: 90vw;
    }
}

.gallary__img {
    transform: scale(0.90);
    transition: all 0.2s;
}

.gallary__controls{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;

    @media (max-width: 767px) {
        margin-top: 0;
    }
}

.gallary__controls-container{
    position: relative;
    display: inline-flex;
}

.swiper-slide-active{
    .gallary__img {
        transform: scale(1);
    }
}

.gallary__swiper-pagination{
    display: flex;
    justify-content: center;
    //width: 100%;
}

.swiper-pagination-bullet{
    width: 12px;
    height: 12px;
    background-color: #fff;
    opacity: 0.3;
    transition: all 0.2s;
    margin-right: 12px;
    &:last-child{
        margin-right: 0;
    }
    &:hover{
        opacity: 1;
        cursor: pointer;
    }
}

.swiper-pagination-bullet-active{
    width: 40px;
    background: linear-gradient(270deg, #387DFB 0%, #00F2FB 100%);
    border-radius: 20px;
    opacity: 1;
}

@media (max-width: 1080px){
    .gallary{
        padding-top: 70px;
        padding-bottom: 70px;
    }
}


@media (max-width: 767px){
    .gallary{
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .gallary__shadows{
        top: -56px;
    }

    .gallary__swiper-pagination{
        margin-top: 30px;
    }

    .swiper-pagination-bullet{
        width: 8px;
        height: 8px;
        margin-right: 8px;
        &:last-child{
            margin-right: 0;
        }
    }
    .swiper-pagination-bullet-active{
        width: 24px;
    }
}