  }
}
.p-download__buttons {
  grid-column: 2 span;
  grid-row: 2/1 span;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
  max-width: 770px;
}
@media (max-width: 640px) {
  .p-download__buttons {
    grid-template-columns: 1fr;
    grid-gap: 25px;
  }
}
.p-download__button {
  margin-right: 50px;
  grid-column: 1 span;
}
.p-download__image {
  grid-column: 1 span;
  position: relative;
}
@media (max-width: 640px) {
  .p-download__image {
    display: none;
  }
}
.p-download__image img {
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  height: auto;
  max-width: none;
  width: calc(100% + 260px);
}
@media (max-width: 1100px) {
  .p-download__image img {
    top: -15%;
    transform: translate(-50%, 0);
    width: calc(100% + 200px);
  }
