.p-button {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  min-height: 70px;
  max-width: 360px;
  width: 100%;
  padding: 10px;
  background: linear-gradient(270deg, #0052E7 0%, #03B2CA 100%);
  border-radius: 500px;
  appearance: none;
  box-shadow: none;
  outline: none;
  border: none;
  min-width: 0;
}
@media (max-width: 960px) {
  .p-button {
    min-height: 60px;
    max-width: 350px;
  }
}
.p-button::before {
  display: none;
  content: "";
  margin-right: 16px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 30px;
}
.p-button_apple::before {
  display: block;
  height: 37px;
  background-image: url("../img/icon-apple.svg");
  transform: translateY(-4px);
}
.p-button_google::before {
  display: block;
  height: 34px;
  background-image: url("../img/icon-market.svg");
}
.p-button_youtube {
  background: none;
  border: 2px solid #fff;
  font-family: "Oswald Regular";
  font-weight: 400;
  font-size: 24px;
}
.p-button_youtube::after {
  display: block;
  content: "";
  width: 16px;
  height: 14px;
  background: url("../img/icon-play.svg") center/contain no-repeat transparent;
  margin-left: 16px;
}

.p-title {
  font-weight: 700;
  font-size: 80px;
  line-height: 1.1;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  background: linear-gradient(270deg, #387DFB 0%, #ABFCFF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (max-width: 1440px) {
  .p-title {
    font-size: calc(5.5vw + 1px);
  }
}
@media (max-width: 640px) {
  .p-title {
    font-size: 36px;
  }
}
@media (max-width: 440px) {
  .p-title {
    font-size: 32px;
  }
}
.p-title_white {
  background-color: #fff;
}

.p-text {
  font-family: "Oswald Regular";
  font-size: 28px;
  line-height: 1.3;
  letter-spacing: 0.015em;
  color: #fff;
  text-transform: uppercase;
}
@media (max-width: 1440px) {
  .p-text {
    font-size: calc(0.8vw + 17px);
  }
}
@media (max-width: 640px) {
  .p-text {
    line-height: 1.4;
    font-size: 22px;
  }
}
@media (max-width: 440px) {
  .p-text {
    line-height: 1.5;
