.p-play {
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
@media (max-width: 1100px) {
  .p-play {
    min-height: auto;
    padding-top: 150px;
    padding-bottom: 150px;
  }
}
@media (max-width: 640px) {
  .p-play {
    padding-top: 0;
    padding-bottom: 100px;
  }
}
.p-play::before {
  display: block;
  content: "";
  position: absolute;
  top: -450px;
  left: -200px;
  background: url("../img/promo/promo-ply-glow.png") center/contain no-repeat transparent;
  height: 2000px;
  width: 2200px;
  z-index: 0;
}
.p-play__inner {
  display: grid;
  grid-template-columns: 1.1fr 0.9fr;
