  position: relative;
  z-index: 2;
}
@media (max-width: 640px) {
  .p-play__side {
    margin-bottom: 20px;
  }
}
.p-play__title {
  margin-bottom: 20px;
}
.p-play__text {
  margin-bottom: 35px;
}
.p-play__image {
  grid-column: 1 span;
  position: relative;
}
.p-play__image img {
